/*--------------------adjustment----------------------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: "Rubik", sans-serif;
  font-weight: normal;
  font-size: 1rem;
  min-width: 20rem;
  position: relative;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  color: #535353;
  background-color: #9DC0BC;
}
.lock body {
  overflow: hidden;
  touch-action: none;
  overflow-behavior: none;
}

html, body {
  height: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow: hidden;
}
@supports (overflow: clip) {
  .main {
    overflow: clip;
  }
}
.main > * {
  min-width: 0;
}
.main .header {
  flex: 0 0 auto;
}
.main .main-content {
  padding-top: 2.6875rem;
  flex: 1 1 auto;
}
.main .footer {
  flex: 0 0 auto;
}

a {
  color: inherit;
}

img {
  vertical-align: top;
}

ul, li {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: inherit;
  font-size: inherit;
}

p {
  margin: 0;
}

input input[type=text],
input[type=email],
input[type=tel],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.input,
input {
  border-radius: 0;
  outline: none;
  display: block;
  width: 100%;
  border: 0 none;
}

textarea {
  resize: none;
  padding: 0 0;
}

.wrapper {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1110px;
}

.wrapper-flex {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1.875rem;
  margin-right: -1.875rem;
}
.wrapper-flex ._flex-col {
  width: calc(25% - 1.875rem);
  margin-left: 0.9375rem;
  margin-right: 0.9375rem;
}
@media only screen and (max-width: 67.5em) {
  .wrapper-flex ._flex-col {
    width: calc(33.3333333333% - 1.875rem);
  }
}
@media only screen and (max-width: 61.9375em) {
  .wrapper-flex ._flex-col {
    width: calc(50% - 1.875rem);
  }
}
@media only screen and (max-width: 47.99875em) {
  .wrapper-flex ._flex-col {
    width: calc(100% - 1.875rem);
  }
}

[class*=__wrapper] {
  margin: 0 auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  max-width: 69.375rem;
}

.hidden {
  display: none;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 52.25%;
}

.ibg img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

._prod-img {
  position: relative;
  padding-bottom: 80%;
}
._prod-img img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

._fit {
  aspect-ratio: 1/1;
  position: relative;
}

._fit ._fit-img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  object-fit: cover;
  object-position: center center;
}

@supports (backdrop-filter: blur(10px)) or (--webkit-backdrop-filter: blur(10px)) {
  .bd-filter-fallback {
    background-color: rgba(255, 255, 255, 0.72);
    backdrop-filter: blur(31px);
  }
}

.toggle-mnu {
  position: fixed;
  right: 1.875rem;
  top: 0.3125rem;
  display: block;
  width: 1.75rem;
  height: 1.75rem;
  padding: 0;
  background-color: transparent;
  border: none;
  z-index: 2;
}
.toggle-mnu span:after, .toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.5625rem;
}
.toggle-mnu span:after {
  top: 1.125rem;
}
.toggle-mnu span {
  position: relative;
  display: block;
}
.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
  width: 100%;
  height: 0.125rem;
  background-color: #000;
  transition: all 0.3s;
  backface-visibility: hidden;
  border-radius: 0.125rem;
}
.toggle-mnu.on span {
  background-color: transparent;
}
.toggle-mnu.on span:before {
  transform: rotate(45deg) translate(-0.0625rem, 0);
}
.toggle-mnu.on span:after {
  transform: rotate(-45deg) translate(0.375rem, -0.4375rem);
}

/*--------------------END adjustment----------------------*/
