/*--------------------header----------------------*/
.header {
  background-color: #fff;
  z-index: 5;
}

.header__wrapper {
  display: flex;
  column-gap: 1.875rem;
  min-height: 3.375rem;
}
@media only screen and (min-width: 47.99875em) {
  .header__wrapper {
    justify-content: space-between;
  }
}

.header__menu {
  position: relative;
}
.header__menu:before {
  content: "";
  display: block;
  height: 3.375rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  z-index: 2;
}
@media only screen and (min-width: 47.99875em) {
  .header__menu:before {
    display: none;
  }
}

@media only screen and (min-width: 47.99875em) {
  .toggle-mnu {
    display: none;
  }
}

@media screen and (max-width: 47.99875em) {
  .menu-header__body {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: all 0.4s;
    transform: translateX(-100%);
    overflow-y: auto;
  }
}
.menu-opened .menu-header__body {
  transform: translateX(0);
}
@media only screen and (max-width: 47.99875em) {
  .menu-header__body {
    background-color: #fff;
  }
}

.header-menu {
  display: flex;
  flex-wrap: wrap;
}
@media only screen and (min-width: 47.99875em) {
  .header-menu {
    align-items: center;
  }
}
@media only screen and (max-width: 47.99875em) {
  .header-menu {
    flex-direction: column;
    padding-top: 3.375rem;
  }
}

.header-menu__item {
  list-style-type: none;
}

.header-menu__link {
  border-bottom: 3px solid transparent;
  transition: all 0.4s;
}
.header-menu__link .icon {
  font-size: 0.9375rem;
  color: #85B6FF;
}
@media (any-hover: hover) {
  .header-menu__link {
    cursor: pointer;
  }
  .header-menu__link:hover {
    border-bottom-color: #FEA901;
  }
}
@media only screen and (max-width: 47.99875em) {
  .header-menu__link.header__item {
    justify-content: center;
    font-size: 1.5rem;
  }
}
@media only screen and (max-width: 47.99875em) {
  .header-menu__link .icon {
    display: none;
  }
}

.header__actions {
  display: flex;
  align-items: center;
  column-gap: 2.0625rem;
  position: relative;
  z-index: 3;
}
@media only screen and (min-width: 67.5em) {
  .header__actions {
    margin-right: -6.25rem;
  }
}

.header__wallet.header__item {
  padding-left: 0;
  padding-right: 0;
}
@media (any-hover: hover) {
  .header__wallet.header__item {
    cursor: default;
  }
}

.header__user {
  border-bottom: 3px solid transparent;
  transition: all 0.4s;
}

.header__item {
  padding-right: 2.0625rem;
  padding-left: 2.0625rem;
  padding-top: 1.125rem;
  padding-bottom: 1.125rem;
  display: flex;
  align-items: center;
  column-gap: 0.625rem;
  color: #000;
  font-family: Inter;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
}
@media (max-width: 67.5em) {
  .header__item {
    padding-right: clamp( 0rem , -0.6875rem  +  3.4375vw , 2.0625rem );
  }
}
@media (max-width: 67.5em) {
  .header__item {
    padding-left: clamp( 0rem , -0.6875rem  +  3.4375vw , 2.0625rem );
  }
}
.header__item .icon {
  font-size: 0.9375rem;
  color: #85B6FF;
}
@media (any-hover: hover) {
  .header__item {
    cursor: pointer;
  }
  .header__item:hover {
    border-bottom-color: #FEA901;
  }
}

/*--------------------END header----------------------*/
/*--------------------products----------------------*/
.products {
  padding-bottom: 2.6875rem;
}

.products__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(17.5rem, 1fr));
  column-gap: 2.875rem;
  row-gap: 2.8125rem;
}
@media (max-width: 67.5em) {
  .products__wrapper {
    column-gap: clamp( 0rem , -0.9583333333rem  +  4.7916666667vw , 2.875rem );
  }
}

.product-card {
  display: flex;
  flex-direction: column;
  border-radius: 1.5625rem;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  padding: 1.5rem;
  padding-bottom: 1.8125rem;
}

.product-card__img {
  flex: 0 0 auto;
  aspect-ratio: 282/242;
  margin-bottom: 1.8125rem;
}

.product-card__body {
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 0.5625rem;
  flex: 1 1 auto;
  font-family: Montserrat;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 0.1px;
}

.product-card__title {
  max-width: 10.625rem;
}

.product-card__price {
  margin-top: auto;
}

.product-card__btn {
  padding: 0;
  background-color: transparent;
  border: none;
  position: absolute;
  right: 0;
  top: 0;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #000;
  font-family: Montserrat;
  font-size: 1.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: 71.429%;
  letter-spacing: 0.2px;
  border-radius: 50%;
}
@media (any-hover: hover) {
  .product-card__btn {
    cursor: pointer;
  }
}

/*--------------------END products----------------------*/